 
@mixin button-shadow($top, $left, $bottom, $blur, $hexcolor, $opacity) {
  -webkit-box-shadow: $top $left $bottom $blur  rgba($hexcolor, $opacity);
  -moz-box-shadow: $top $left $bottom $blur rgba($hexcolor, $opacity);
  box-shadow: $top $left $bottom $blur rgba($hexcolor, $opacity);
}

@mixin hex-rgba($hexcolor, $opacity) {
    background: $hexcolor;
    background: rgba($hexcolor, $opacity) !important;
}

@mixin transitions($whats: all, $time: 0.3s, $how: ease-in-out) {

    -moz-transition: $whats $time $how;
    -ms-transition: $whats $time $how;
    -o-transition: $whats $time $how;
    -webkit-transition: $whats $time $how;
    transition: $whats $time $how;
}

@mixin transitions2($param1,$param2) {

    -moz-transition: $param1,$param2 ;
    -ms-transition: $param1,$param2 ;
    -o-transition: $param1,$param2 ;
    -webkit-transition: $param1,$param2 ;
    transition: $param1,$param2 ;
}


@mixin transforms($what) {
  -webkit-transform: $what;
  -moz-transform: $what;
  -ms-transform: $what;
  -o-transform: $what;
  transform: $what;
}

@mixin border-radius($radius) {
   -ms-radius: $radius;
    -o-radius: $radius;
  -webkit-border-radius: $radius;
  -moz-border-radius: $radius;
  border-radius: $radius;
}
  
@mixin background() {
  background-size: cover;
  background-position: center;
  background-repeat: no-repeat;
}