html, body, div, span, applet, object, iframe,

h1, h2, h3, h4, h5, h6, p, blockquote, pre,

a, abbr, acronym, address, big, cite, code,

del, dfn, em, img, ins, kbd, q, s, samp,

small, strike, strong, sub, sup, tt, var,

b, u, i, center,

dl, dt, dd, ol, ul, li,

fieldset, form, label, legend,

table, caption, tbody, tfoot, thead, tr, th, td,

article, aside, canvas, details, embed,

figure, figcaption, footer, header, hgroup,

menu, nav, output, ruby, section, summary,

time, mark, audio, video {

  margin: 0;

  padding: 0;

  border: 0;

  font-size: 100%;

  font: inherit;

  vertical-align: baseline; }


article, aside, details, figcaption, figure,
footer, header, hgroup, menu, nav, section {

  display: block; }



body {

  line-height: 1; }



ol, ul {

  list-style: none; }



blockquote, q {

  quotes: none; }



blockquote:before, blockquote:after,

q:before, q:after {

  content: '';

  content: none; }



table {

  border-collapse: collapse;

  border-spacing: 0; }



body {

  font-weight: 400;
  font-size: 14px;
  line-height: 26px;

  letter-spacing: 0; }



img {

  max-width: 100%; }



a,

a:hover,

a:focus {

  cursor: pointer !important;

  outline: none;

  text-decoration: none; }



h1,

h2,

h3,

h4,

h5,

h6 {


  margin: 0;

  padding: 0; }



ul, li {

  margin: 0;

  padding: 0;

  list-style: none; }



strong {

  font-weight: 700; }

body { 
	font-family: $font-family; 
	margin: 0; 
	padding: 0; 
	font-size: 1rem; 
	color: $body;
	line-height: $line-xl ;
}

h1, h2, h3, h4, h5, h6, p, span, ul, li, a, ol {
	padding:0;
	margin:0;
}

h1, h2, h3, h4, h5, h6 {
	color: $dark-gray;
	font-weight: $font-regular;
	font-family: $font-family;
	margin-bottom: 0;
}
h1 {
	font-size: 2.558rem;
	line-height: 2.558rem;
	font-weight: 700;
}
h2 {
	font-size: 2.288rem;
	line-height: 2.288rem;
	font-weight: $font-bold;
}

h3 {
	font-size: 1.858rem;
	line-height: 1.858rem;
	font-weight: $font-bold;
}

h4 {
	font-size: 1.571rem;
	line-height: 1.858rem;
}

h5 {
	font-size: 1.286rem; 
	line-height: 1.858rem;
}
h6 {
	font-size: 1.143rem;
	line-height: 1.858rem;
}
p{
  font-size: 16px;
}


/* This only works with JavaScript, 
        if it's not present, don't show loader */

.no-js #loader {
    display: none;
}

.js #loader {
    display: block;
    position: absolute;
    left: 100px;
    top: 0;
}

.se-pre-con {
    position: fixed;
    left: 0px;
    top: 0px;
    width: 100%;
    height: 100%;
    z-index: 9999999;
    background: url(../img/loader_ico.gif) center no-repeat #fff;
}