 /*button style*/
 .btn.btn-style1{
    position: relative;
    z-index: 1;
    background-color: $btn-color;
    color: $white;
    padding: 14px 26px;
    font-weight: $font-bold;
    font-size: $font-base;
    font-family: $font-family2;
    overflow: hidden;
    @include transitions (all, 0.3s, ease-in);
    &:hover{
        color: $btn-color;
    }
    &:after{
        content: '';
        width: 0;
        height: 100%;
        position: absolute;
        left: 0;
        top: 0;
        z-index: -1;
       @include transitions (all, 0.3s, ease-in);
       background-color: $white;
    }
    &:hover:after{
        width: 100%;
    }
    i{
        padding-left: 10px;
    }
    
}

 /*title style*/
 .title_sec{
    width: 100%;
    h3{
        font-family: $font-family;
        font-weight: $font-bold;
        color: $black-light;
        font-size: 42px;
        line-height: 1.3em;
    }
    p{
        color: $text-color;
        font-size: $font-base;
        font-weight: $font-regular;
        font-family: $font-family2;
        padding-bottom: 30px;
        padding-top: 20px;
    }
 }